// , , , @media-...
//     .

.comments {
  margin: 35px 0 0;
  padding-bottom: 10px;

  &:not(:last-child) {
    margin-bottom: 30px;
  }

  @media (min-width: $screen-md) {
    display: flex;
    align-items: flex-start;
  }

  &__title {
    margin: 0 0 25px;
    padding: 0;
    font-weight: 500;
    font-size: $font-size;

    @media (min-width: $screen-md) {
      width: 200px;
      min-width: 200px;
      padding-right: 15px;
    }

    @media (min-width: $screen-osx) {
      width: 260px;
      min-width: 260px;
    }
  }

  &__list {

    @media (min-width: $screen-md) {
      flex-grow: 1;
    }
  }

  &__view {
    display: inline-block;
    margin-top: 20px;
  }
}
